<template>
  <div class="components_search">
    <div class="search">
      <img src="../../images/myinfo/search_not.png">
      <input type="search" placeholder="房产知识有疑问？来搜搜吧~" v-model="searchStr" />
    </div>
    <i @click="gotoSearch()">搜索</i>
  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore, setStore,removeStore} from '../../components/common/mUtils'
export default {
  data(){
    return{
      userID:'',
      userToken:'',
      userInfo:null,
      isLogin:false,
      fixed:false,
      searchStr:'',
      typeArr:[],
    }
  },
  props: ['title','isfixed',],
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    if (this.userID) {
      this.isLogin = true
      //this.getUserData()
    }
  },
  mounted(){
    //this.getAskType()
  },
  components:{
  },
  methods: {
    handleBack(){
      this.$router.go(-1);
    },
    // 跳转到板块
    gotoBk(){
      this.$router.push('/arealist')
    },
    gotoMyinfo(){
      this.$router.push('/myAsk_pc')
    },
    gotoLogin(){
      this.$router.push('/login_pc')
    },
    gotoUrl(url){
      window.location.href=url;
    },
    searchTypeAction(){
      this.showSearchPop = !this.showSearchPop;
    },
    setSearchType(item){
      this.showSearchPop = false;
      this.searchKey = item;
    },
    gotoSearch(){
      if (this.searchStr != '') {
        let str = encodeURI(encodeURI(this.searchStr));
        this.$router.push({path: '/search', query:{keyStr:str,type:1,sid:this.$route.query.tid}})
      }else {
        this.$router.push({path: '/search', query:{type:1,sid:this.$route.query.tid}})
      }
    },
  },

}
</script>

<style scoped>

/***** 顶部的搜索栏 *****/
.components_search{
  height: 40px;
  padding: 0 8px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(245,245,245);
}

.components_search .search{
  background-color: #fff;
  height: 30px;
  flex-grow: 1;
  border-radius: 5px;
  /*margin-left: 10px;*/
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 0 8px;
}

.components_search .search img{
  width: 14px;
  height: 14px;
  margin-right: 3px;
}

.components_search .search input{
  background-color: #fff;
  font-size: 14px;
  width: 100%;
}

.components_search i{
  /*background-color: #ffc107;*/
  color: #999;
  border-radius: 5px;
  padding: 6px 6px;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
}



</style>



