<template>
  <div id="myAsk">
    <mt-header :title="title" fixed>
      <mt-button  icon="back" slot="left" @click="handleBack()">返回</mt-button>
      <mt-button  slot="right" @click="gotoPostAsk()">提问</mt-button>
    </mt-header>

    <div class="lise_40"></div>
    <!-- <datepicker v-model="date" language="ch"></datepicker> -->
    <!-- 我的报 Warp -->
    <section class="search_wrap"><Search></Search></section>
    <div id="report_Warp">

      <!-- ****** 全部 ****** -->
      <li v-for="item in list" >
        <router-link :to="{path: '/askDetail', query:{qid: item.q_id}}">
          <AskList  :itemData="item"></AskList>
        </router-link>
      </li>
    </div>


    <div class="noData" v-if="list.length == 0">数据为 0 条</div>
    <div @click="seeing">
      <div class="loading-start" v-if="loading"></div>
      <span class="more" v-if="!complete && !loading">点击加载更多</span>
    </div>

  </div>
</template>

<script>
import { Toast,Indicator,Loadmore,DatetimePicker} from 'mint-ui'
import {getStore, setStore} from '../../components/common/mUtils'
import AskList from '../home/children/AskList'
import Search from '../../components/common/Search'
import $ from 'webpack-zepto'
export default {
  components:{
    AskList,
    Search
  },
  data(){
    return{
      userID:'',
      userToken:'',
      list:[],
      complete: false, // 数据是否加载完成
      loading: false, // 是否在请求中
      curPage:1, // 当前页
      pageSize:10, // 默认显示10数据
      tid:2,
      loading:false,
      complete:true,
      title:'',
      searchKey:{
        page:1,
        complete:false,
      },
    }
  },
  created(){
  },
  mounted(){
    this.tid = this.$route.query.tid
    this.title = decodeURI(decodeURI(this.$route.query.name))
    // this.pageKey = this.$route.name
    // if (window.window.sessionStorage.pageKey == this.pageKey) {
    //     this.list = JSON.parse(window.window.sessionStorage.List);
    //     this.searchKey = JSON.parse(window.window.sessionStorage.searchKey);
    //     this.complete = this.searchKey.complete
    //     this.$nextTick(() => $(window).scrollTop(window.window.sessionStorage.scrollTop));
    // } else {
    //     this.getData()
    // }
    //
    this.getData()
  },
  filters:{
  },
  beforeRouteEnter (to, from, next) {
     // 在渲染该组件的对应路由被 confirm 前调用
     // 不！能！获取组件实例 `this`
     // 因为当钩子执行前，组件实例还没被创建
     if (from.name != 'AskDetail') {
       window.window.sessionStorage.removeItem('List');
       window.window.sessionStorage.removeItem('pageKey');
       window.window.sessionStorage.removeItem('scrollTop');
     }
     next()
  },
  beforeRouteLeave(to, from, next) {
   // 导航离开该组件的对应路由时调用
   // 可以访问组件实例 `this`
   if (to.name == 'AskDetail') {
      // 当前滚动条位置
      window.window.sessionStorage.scrollTop = $(window).scrollTop()
      // 当前页面主题数据
      window.window.sessionStorage.List = JSON.stringify(this.list)
      // 保存页面key
      window.window.sessionStorage.pageKey = this.pageKey
      // 当前tab
      this.searchKey.complete = this.complete
      window.window.sessionStorage.searchKey = JSON.stringify(this.searchKey)
    }
    $(window).off('scroll');
   next()
  },
  methods: {
    handleBack(){
      this.$router.go(-1)
    },
    gotoAddress(path){
      this.$router.push(path)
    },
    gotoPostAsk(){
      this.userID = getStore('userID')
      if (this.userID) {
        this.$router.push('/publishAsk')
      }else {
        this.$router.push('/login')
      }
    },
    getData(){
      Indicator.open();
      let pStr = '?sid='+this.tid+'&curPage='+this.searchKey.page+'&pageSize='+this.pageSize
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/list'+pStr
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          if (arr.length > 0) {
            // 如果小于10条就是加载完了
            this.complete = arr.length < 10 ? true :false
            arr.forEach((item) => this.list.push(item))
          } else {
            this.complete = true
            this.allLoaded = true
          }
        }
        this.searchKey.page++
        this.loading = false // 请求完成
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    loadBottom(){
      // 一定要实现的方法
      console.log('loadBottom')
    },
    seeing(){
      if (this.searchKey.page === 1) {
        return // 防止偶然性的第一个重复请求的bug
      }
      this.getData()
    }
  },
  watch:{
  }

}
</script>

<style>
/*引入公共样式*/
@import '../../style/common/style.css';

#myAsk{
  background-color: #f5f5f5;
}

#myAsk .noData{
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}



#myAsk .header_more{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

#myAsk .header_more .search{
  /*margin-right: 15px;*/
}


#report_Warp {
  /*margin-top:10px;*/
}

#report_Warp .range_time{
  background-color: #fff;
  height: 36px;
  display: flex;
  align-items: center;
  padding-left:10px;
  font-size: 13px;
}

#report_Warp .range_time i{
  color: #26a2ff;
  padding: 3px 6px;
  font-style: normal;
  font-size: 13px;
  line-height: 13px;
  margin-right: 10px;
  border: 1px solid #26a2ff;
}

#report_Warp .range_time em{
  background-color: #ffc107;
  font-size: 13px;
  font-style: normal;
  padding: 3px 5px;
  color: #fff;
  border-radius: 2px;
}

#report_Warp .ML{
  margin-left: 10px;
}



.loginBtnTop{
  border-top: 1px solid #EEE;
  height: 30px;
}

.loading-start {
  margin: 2px auto 20px auto;
  position: relative;
  animation: rotate-forever 1s infinite linear;
  height: 30px;
  width: 30px;
  border: 4px solid #80bd01;
  border-right-color: transparent;
  border-radius: 50%;
}

.more{
  height: 40px;
  line-height: 40px;
}

/*弹出框*/
.calendar-dialog{
    position: absolute;
    left:0;
    top:0;
    right:0;
    bottom:0;
}

.calendar-dialog-mask{
    background:rgba(000,000,000,.8);
    width:100%;
    height:calc(100vh);
}

.calendar-dialog-body{
    background: #fff;
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    padding:20px;
    border: 1px solid #eee;
    border-radius: 2px;
}

@keyframes rotate-forever {
  0% {
    transform: rotate(0deg)
  }
  100% {
    transform: rotate(360deg)
  }
}

</style>



